import {
    LOGIN_ON,
    LOGIN_OUT
} from './actions/actionType'

const initialState = {
    login: false
}

export default function(state=initialState, action) {
    switch(action.type) {
        case LOGIN_ON:
            return {
                ...state,
                login: true
            }
        case LOGIN_OUT:
            return {
                ...state,
                login: false
            }
        default:
            return state
    }
}